select {
  background-color: transparent; }

table, .table {
  width: 100%;
  border-collapse: collapse; }

em {
  font-style: normal; }

img {
  vertical-align: middle;
  max-width: 100%; }

legend {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  background: 0 0;
  font-size: 0;
  color: transparent;
  line-height: 0; }

table caption {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

table th {
  font-weight: 500; }

.cf::after {
  content: '';
  clear: both;
  display: table; }

body {
  letter-spacing: -0.4px;
  overflow-x: hidden;
  color: #333; }

hr {
  visibility: hidden;
  margin-top: 0;
  margin-bottom: 0; }

.slick-dots button {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

textarea {
  font-size: 16px; }

ol li::marker {
  color: #333; }

video {
  display: block;
  width: 100%; }

